* {
  box-sizing: border-box;
  -moz-box-sizing: border-box;
}
html {
  font-size: 16px;
  font-weight: 300;
  background-attachment: fixed;
  background-color: white;
}
body {
  font-family: 'Helvetica Neue', Helvetica, sans-serif;
  margin: 0 auto 75px auto;
  padding: 35px 60px 15px 60px;
  max-width: 1024px;
  color: #4E5260;
  line-height: 2;
}
body.adorn-fullscreen {
  max-width: none;
}
@media all and (min-width: 1440px) {
  body.adorn-fullscreen {
    margin-left: 220px;
  }
  .adorn-sidebar-off body.adorn-fullscreen {
    margin-left: auto;
  }
}
@media all and (max-width: 1440px) and (min-width: 1048px) {
  body {
    margin-left: 220px;
  }
  .adorn-sidebar-off body {
    margin-left: auto;
  }
}
@media all and (max-width: 1048px) {
  body {
    border: none;
    padding: 10px;
    margin: 0 !important;
  }
}
@media all and (max-width: 650px), screen and (max-device-width: 480px) {
  body {
    margin: 0;
  }
}
.button,
button {
  padding: 10px;
  border-radius: 5px;
  background-color: #4E5160;
  display: inline-block;
  border: none;
  text-decoration: none;
  color: white;
  cursor: pointer;
  line-height: 1;
}
.button:hover,
button:hover {
  opacity: 0.9;
  background: #626574;
}
.button.green,
button.green {
  background-color: #10A54A;
}
.button.blue,
button.blue {
  background-color: #0F6AB4;
}
.button.red,
button.red {
  background-color: #F00;
}
blockquote {
  background-color: #eee;
  border: 1px solid #c7c7c7;
  padding: 15px 50px;
  margin: 0 -50px 35px -50px;
  text-align: justify;
  position: relative;
}
body.adorn-fullscreen blockquote {
  padding: 15px 60px;
  margin: 0 -60px 35px -60px;
}
blockquote {
  font-style: italic;
  border: none;
}
blockquote p {
  line-height: inherit;
}
blockquote:after,
body > form:after {
  margin-top: 1px;
  border-color: #aaa #aaa transparent transparent;
  left: -1px;
}
blockquote:before,
body > form:before {
  margin-top: 1px;
  border-color: #aaa transparent transparent #aaa;
  right: -1px;
}
footer {
  width: 100%;
  bottom: -3em;
  color: black;
  font-size: small;
  text-align: center;
  margin-top: 5em;
}
a {
  cursor: pointer;
  color: #0C50C7;
}
h1,
h2,
h3,
h4 {
  margin-top: 4em;
  line-height: 1;
  font-weight: 300;
}
h1 {
  margin-bottom: 0;
  padding: 0;
  font-size: 4em;
}
h2 {
  border-bottom: 1px solid #dcdcdc;
  font-size: 1.5em;
  margin-left: -50px;
  margin-right: -50px;
  padding: 0.5em;
  padding-left: 50px;
  padding-right: 50px;
}
h1 + h2 {
  margin-top: 0;
}
/**
 * FORM
 */
body > form {
  margin: 0 -60px 20px;
  padding: 20px 60px;
  background-color: #fafa52;
  box-shadow: 0 10px 10px -10px #000000;
  position: relative;
}
body > form > h4,
body > form > h3,
body > form > h2 {
  margin-top: 0;
  padding-top: 0;
}
q {
  color: green;
}
i {
  color: blue;
}
@media all and (max-width: 1048px) {
  blockquote,
  body > form {
    padding: 10px;
    margin: 0;
  }
  h2 {
    margin-left: -10px;
    margin-right: -10px;
    max-width: none;
  }
}
@media all and (max-width: 650px), screen and (max-device-width: 480px) {
  * {
    max-width: 100%;
  }
  h1 {
    text-indent: 0;
    font-size: 3em;
  }
  h2 {
    text-indent: 0;
    padding: 0.5em;
  }
  pre,
  .pre {
    overflow-x: auto;
  }
  ul {
    padding-left: 20px;
  }
}
pre,
.pre,
code {
  color: green;
  font-family: monospace;
  background-color: #f4f4f4;
  border: 1px solid #e4e4e4;
  word-break: break-word;
}
pre > pre,
pre > .pre,
pre > code,
.pre > pre,
.pre > .pre,
.pre > code,
code > pre,
code > .pre,
code > code {
  border: none;
}
pre,
.pre {
  display: block;
  border-left-width: 10px;
  white-space: pre;
  padding: 15px;
  margin: 0 0 35px 0;
  max-width: 100%;
  overflow: auto;
  word-wrap: normal;
  -moz-tab-size: 2;
  -ms-tab-size: 2;
  tab-size: 2;
}
@media all and (max-width: 1048px) {
  pre,
  .pre {
    font-size: 0.9em;
  }
}
@media all and (max-width: 650px), screen and (max-device-width: 480px) {
  pre,
  .pre {
    margin: 0 -10px;
    width: auto;
    max-width: none;
    font-size: 0.7em;
  }
}
pre[title].before,
.pre[title].before,
pre[title]:before,
.pre[title]:before {
  position: absolute;
  margin-top: -30px;
  content: attr(title);
  background: #E4E4E5;
  margin-left: -25px;
  padding: 0 10px;
}
prepre:empty:before,
.prepre:empty:before {
  content: "[waiting for it]";
  color: #4E5160;
}
pre[contenteditable],
.pre[contenteditable] {
  position: relative;
}
pre[contenteditable]:focus,
.pre[contenteditable]:focus {
  color: black;
  background-color: white;
}
pre[contenteditable]:after,
.pre[contenteditable]:after {
  content: "edit";
  position: absolute;
  top: 0;
  right: 0;
  color: red;
  background-color: #E4E4E4;
  padding: 0 10px;
}
pre[contenteditable]:focus:after,
.pre[contenteditable]:focus:after {
  background-color: white;
}
pre + button,
.pre + button {
  margin-top: -60px;
  position: absolute;
}
pre.tryit,
.pre.tryit,
pre.tryitoffline,
.pre.tryitoffline {
  margin-bottom: 50px;
}
script.pre:not(:empty):after {
  display: block;
  content: "</script>";
  color: #a4a4a5;
}
script.pre:not(:empty):before {
  content: "<script>";
  color: #a4a4a5;
}
script.pre[type]:after {
  content: none;
}
script.pre[type]:before {
  content: "<" attr(type) ">";
  color: #a4a4a5;
}
script.pre[src]:after {
  content: "<script src='" attr(src) "'></script>";
}
script.pre[src],
link.pre[src] {
  cursor: pointer;
}
script.pre[src]:hover,
link.pre[src]:hover {
  text-decoration: underline;
}
style.pre:before {
  content: "/* CSS stylesheet */";
  color: #a4a4a5;
}
link.pre:after {
  content: "<link href='" attr(href) "' rel='stylesheet'/>";
}
/**
 * Table, compatibility
 */
table,
.table {
  border-collapse: collapse;
  border-spacing: 0;
  width: 100%;
  background-color: inherit;
}
@media all and (max-width: 1048px) {
  table,
  .table {
    font-size: 0.9em;
  }
}
@media all and (max-width: 650px), screen and (max-device-width: 480px) {
  table,
  .table {
    font-size: 0.7em;
    display: block;
    overflow: auto;
    margin: 0 -10px;
    padding: 0 10px;
    width: auto;
    max-width: none;
  }
}
.table {
  display: table;
}
.table-header {
  display: table-header-group;
}
.table-footer {
  display: table-footer-group;
}
.table-body {
  display: table-row-group;
}
.table-row {
  display: table-row;
}
tr.break th,
.table-row.break th,
.table-header tr.break .table-cell,
.table-header .table-row.break .table-cell {
  background-color: #f8f8f8;
}
tr.break:first-child,
.table-row.break:first-child {
  border-top: 0;
  border-bottom: 0;
  border-right: 0;
  vertical-align: middle;
}
tr.break:last-child,
.table-row.break:last-child {
  border-left: 0;
}
td,
th,
.table-cell {
  text-align: left;
  border-bottom: 1px solid #D2D2D2;
  padding: 4px 10px 4px 6px;
  vertical-align: top;
}
thead td,
th,
.table-header .table-cell {
  border-bottom-width: 3px;
}
table td pre,
.table-cell pre {
  margin: 0;
  padding: 0;
  background-color: transparent;
  border: none;
  white-space: pre-wrap;
}
td.yes,
td.no {
  vertical-align: middle;
}
.yes:before,
.no:before {
  color: white;
  text-transform: uppercase;
  display: inline-block;
  border-radius: 5px;
  padding: 3px 10px;
}
.yes:before {
  content: "\02714";
  background-color: #10A54A;
}
.no:before {
  content: "\02716";
  background-color: red;
}
tbody th {
  padding-left: 10px;
}
thead + tbody tr th,
tbody tr:not(:first-child) th {
  border-bottom-width: 1px;
}
tbody th p {
  font-weight: normal;
}
td.partial,
span.partial {
  background-color: #20A84D;
}
